body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
    margin-bottom: 50px;
}

.buttons_container{
  text-align: center;
}

.buttons_first {
  margin-right: 10px;
}

.nav-btn{
  text-align: center;
}
.header{
    background-color: lightgray;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

span:first-child{
    margin-left: 20px;
}

.header__buttons{
    margin-left: auto;
    margin-right: 20px;
}
.table_foto{
    width: 15%;
}

.table_foto img{
    width: 139px;
    height: 181px;
}

.table_number{
    width: 2%;
    padding: 0;
}

.table_name{
    width: 15%;
}

.table_surname{
    width: 15%;
}

.table_age{
    width: 10%;
}

.buttons_container{
    width: 15%;
}

tr td{
    font-size: 20px;
    text-align: center;
    vertical-align: middle !important;
}
tr th{
    text-align: center;
    cursor: default;
}

.htmlSymbol{
    margin-left: 5px;
    display: inline;
    font-size: 12px;
}

.table_sort:hover{
    cursor: pointer;
}
.table-header-cell {
    display: -webkit-flex;
    display: flex;
}

.table-header-cell__arrow {
    color: gray;
}

.table-header-cell__arrows{
    font-size: 12px;
    margin: 0;
    padding: 0;
    line-height: 1;
    margin-left: 10px;
}

.table-header-cell__arrow_active {
    color: black;
}
.mainTitle{
    background-color: #FFC941;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 20px 0;
    border-bottom: 5px solid red;
}

.mainTitle img{
    width: 150px;
    height: 150px;
}

.mainTitle span{
    margin-left: 50px;
    font-size: 80px;
    font-weight: bold;
}
.newPlayerContainer{
    display: -webkit-flex;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.newPlayerBackground{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: grey;
    opacity: 0.9;
}

.newPlayer{
    margin: auto;
    width: 800px;
    background-color: white;
    z-index: 100;
}

.newPlayer__header{
    font-size: 30px;
    background-color: #FFC941;
    border-bottom: 5px solid red;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

form{
    padding: 20px;
    text-align: center;
    font-size: 20px;
}

.newPlayer__buttons{
    margin-top: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin-bottom: 20px;
}

.row{
    margin-bottom: 40px;
}

.fileInput{
    font-size: 16px;
}

.newPlayer__middle-button{
    margin-right: 20px;
    margin-left: 20px;
}

